.single-post {
  position: relative;
}
.single-post h1,
.single-post h2,
.single-post h3,
.single-post h4,
.single-post h5,
.single-post h6 {
  margin: 2rem 0 0;
}
.single-post .post {
  padding: 1rem;
  background: #fff;
  position: relative;
}
.single-post .post img {
  max-width: 100%;
}
.post-full-image {
  height: 35vh;
  width: 100%;
  background-size: cover;
  background-position: center;
  margin: 0;
}
.single-post .post-title {
  font-size: 2rem;
  margin: .5rem 0;
}
.single-post .post-meta {
  display: flex;
  align-content: space-between;
  align-items: center;
  border-bottom: 1px dotted #ddd;
  padding-bottom: .5rem;
}
.pocket-btn {
  height: 20px;
  width: 60px;
  margin-left: .5rem;
  padding-left: .5rem;
  border-left: 1px solid #ddd;
  background: url(../../images/pocket_button.png) top right no-repeat;
  background-size: 60px;
}
.pocket-btn:hover,
.pocket-btn:focus {
  background-position: 100% -20px;
}
@media (--breakpoint-xs) {
  .single-post .post-title {
    font-size: 2.4rem;
  }
  .single-post .post-content {
    font-size: 1.3rem;
  }
}
@media (--breakpoint-sm) {
  .single-post .post-title {
    font-size: 3rem;
  }
  .single-post .post-meta {
    font-size: 1.2rem;
  }
  .single-post .post-content {
    font-size: 1.4rem;
  }
}
@media (--breakpoint-md) {
  .post-full-image {
    height: 50vh;
  }
  .single-post .post-title {
    font-size: 2.2rem;
  }
  .single-post .post-content {
    font-size: 1.2rem;
  }
}
@media (--breakpoint-xl) {
  .post-full-image {
    position: absolute;
    top: 0;
    left: 0;
    height: 60vh;
  }
  .has-feature-image {
    padding-top: 45vh;
  }
  .single-post .post-meta {
    font-size: 1.1rem;
  }
  .single-post .post {
    margin-left: 2rem;
    max-width: 55rem;
    padding: 1.5rem 2rem;
  }
}
@media (--breakpoint-xxl) {
  .single-post .post {
    margin-left: 4rem;
    padding: 2rem 4rem;
    max-width: 60rem;
  }
  .single-post .post-title {
    font-size: 3rem;
    margin-bottom: 1rem;
  }
  .single-post .post-content {
    font-size: 1.3rem;
    margin-top: 2rem;
  }
}

.subscribe {
  text-align: center;
  border: 1px solid rgba(0,0,0,.0625);
}
.subscribe .form-group {
  margin-bottom: 1rem;
  width: 100%;
}
.subscribe .subscribe-email {
  width: 100%;
}
@media (--breakpoint-sm) {
  .subscribe .form-group {
    margin-bottom: 0;
    width: 70%;
    float: left;
  }
  .subscribe .subscribe-email {
    border-radius: 4px 0 0 4px;
  }
  .subscribe form button {
    width: 30%;
    float: left;
    border-radius: 0 4px 4px 0;
  }
}

.read-next {
  max-width: 48rem;
}
.read-next-story a {
  color: var(--gray);
  background: transparent url('../../images/chevron-right.svg') 100% 50% no-repeat;
  background-size: 32px;
  min-height: 32px;
}
.read-next-story.prev a {
  background: transparent url('../../images/chevron-left.svg') 0 50% no-repeat;
  background-size: 32px;
}
.read-next-story a:hover {
  color: var(--black);
}

.newsletter-signup {
  margin: 3rem 0 1rem;
  padding: 1.25rem;
  background: #fafafa;
  border: 1px dashed #2fad84;
}
.newsletter-signup label {
  font-size: 1.5rem;
  font-family: var(--heading-font-family);
}
@media (--breakpoint-sm) {
  .newsletter-signup label {
    font-size: 1.8rem;
  }
}
.newsletter-signup .signup-items {
  display: flex;
  margin-top: 1rem;
}
.newsletter-signup .signup-items .email-input {
  flex: 1 0;
}
.newsletter-signup .signup-items .email-input input {
  width: 100%;
  font-size: 1.3em;
  line-height: 1.5;
  padding: .25em;
  box-sizing: border-box;
  border-right: 0;
  border-radius: 5px 0 0 5px;
}
.newsletter-signup .signup-items .button {
  width: 100%;
  font-size: 1.3em;
  line-height: 1.5;
  padding: .25em 1rem;
  box-sizing: border-box;
  background: #2fad84;
  color: #efefef;
  border: 1px solid rgba(0,0,0,.125);
}
.newsletter-signup .action-response {
  margin-top: 1rem;
  font-size: 1.5em;
  text-align: center;
}
.newsletter-signup .powered-by {
  text-align: right;
  font-size: .9em;
  margin-bottom: 0;
}